import {
	toMoney
} from '@/vendor/filters.js'
export default {
	filters: {
		toMoney
	},
	watch: {
		money(newValue, oldValue) {
			console.log("test format");
			if(/^\d*.?\d{0,2}$/.test(newValue)){
				this.valid = true;
			}else{
				this.valid = false;
			}
		}
	},
	data() {
		return {
			availableMoney: 0,
			money:"",
			valid:true,
		}
	},
	
	methods: {
		
		withdraw(){
			
			if(this.valid&&!!this.money){
				uni.showModal({
					content: '您确认进行提现吗？',
					showCancel: true,
					success: res => {
						if(res.confirm){
							//TODO
							uni.redirectTo({
								url: './success',
							});
						}
					},
				});
			}
			else{
				this.$$.showErr("输入金额错误！");
			}
			
		}
	}
}
